import React, { useState } from 'react';
import kisu from '../gifs/cute-kitty-best-kitty.gif';
import kiisu from '../gifs/typing-cat-gif.gif';
import '../App.css';
import './Mau.css'

const Mau = () => {
  let mou = new Date().getHours()
  let [aika, setAika] = useState(mou);

  if (aika < 23 && aika > 9) {
    return (
      <div className="wrap">
        <button onClick={() => setAika(aika = 23)}>
          Mau
        </button>
        <img src={kiisu} alt="loading..." className="kisu"></img>
      </div>
    )
  }
  else {
    return (
      <div  className="wrap">
        <button onClick={() => setAika(aika = 10)}>
          Miu
        </button>
        <img src={kisu} alt="loading..." className="kisu"></img>
      </div>
    )
  }
}

export default Mau
