import React, { useState, useEffect, useRef } from "react";
import './Kana.css'

const Kana = () => {
  const [isActive, setActive] = useState("false");
  const [isKana, setKana] = useState("false");
  const [count, setCount] = useState(0);

  useInterval(() => {
    setActive(!isActive);
  }, 400);
  
  const handleClick = () => {
    setKana(!isKana);
  }

  return (    
    <div>
      <button
        className="kana-btn"
        onClick={handleClick}>
        Kana
        </button>
      <div className={`kana ${isActive ? "flip" : ""} ${isKana ? "hidden" : ""}`} />
    </div>
  )
}

function useInterval(callback, delay) {
  const savedCallback = useRef();

  // Remember the latest function.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}

export default Kana
